*
{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}
.quiz-container{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: lightgreen ;
}
.quiz-container_question{
    border-radius: 2rem;
    height: 400px;
    width: 50%;
    margin: 0 auto;
    text-align: center;
    background-color: darkgreen;
     color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-size: 1.4rem;
    box-shadow: 0px 10px 40px #999;
}
.quiz-container_options{
    padding: 2rem;
    border-top: 1px solid #ccc;
    margin-left: 80px;
    width: 600px;
}
.quiz-container_ul{
    padding: 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 1rem;
    list-style-type: none;
}

.quiz-container li{
    padding: 1rem;
    background-color: white;
    color: #000;
    cursor: pointer;
    border-radius: 0.5rem;
    transition: all 0.5s ease;
}
.quiz-container_li:hover{
    box-shadow: 0px 1px 10px #ffff;
}
.reset-button{
    background: green;
    border: none;
    padding: 15px;
    border-radius: 5px;
    font-size: 30px;
    font-weight: 100;
    margin-top:200px ;
    
}